import * as React from "react";
import * as PropTypes from "prop-types";

import NamespaceWrapper, { NamespaceWrapperProps } from "../utils/NamespaceWrapper";

export interface LoaderProps extends NamespaceWrapperProps {
    type ?: "pulse" | "circle" | "linear" | "dot";

    className ?: string;
}

export default class Loader extends NamespaceWrapper<LoaderProps, {}> {
    public static displayName = "Loader";

    public static propTypes = {//
        "type": PropTypes.oneOf(["pulse", "circle", "linear", "dot"]), //
        "className": PropTypes.string
    };

    static defaultProps: LoaderProps = {
        type: "pulse"
    };

    public render() {
      const className = this.classes(
        "Loader",
        this.props.type && "Loader--" + this.props.type,
        this.props.className
      );

        return <div className={className}>
            <span className={this.classes("Loader__indicator")}></span>
            {this.props.children}
        </div>;
    }
}
